@import url(https://fonts.googleapis.com/css2?family=Yusei+Magic&display=swap);
body {
  font-family: 'Yusei Magic', sans-serif !important;
}

:root {
  --primary-white:#fff;
  --primary-green:rgb(0,191,166);
}

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}
.navbar {
    z-index: 2;
}
.bg-dark {
    background-color: black !important;
}
.logo {
    max-height: 1.5rem;
    margin-right: 1.25rem;
}

nav a.nav-link {
    letter-spacing: 0.0375rem;
    cursor: pointer;
}

.navbar-nav >li >a {
    color: rgb(0,191,166) !important;
    margin-right: 0.75rem;
    margin-left: 0.75rem;
    transition: all 0.2s ease-in-out;
}

.navbar-brand {
    max-width: 17.25rem;
    padding: 0 !important;
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
    border-bottom: 0.1875rem solid rgb(0,191,166);
}

.navbar-nav > li > a:hover {
    border-bottom: 0.1875rem solid rgb(0,191,166);
}
.header-wrapper {
  position: relative;
  background: url(/resume/static/media/ProgressiveApp.889e9e1c.svg), rgb(0, 0, 0);
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: bottom;
  height: 90vh;
  color: white;
}

strong {
  color: var(--primary-green);
}

@media screen and (max-width: 960px) {
  .header-wrapper {
    background-position: center !important;
  }
}

@media screen and (max-width: 960px) {
  .main-info > h1 {
    font-size: 1.25rem !important;
  }
}

@media screen and (max-width: 960px) {
  .typed-text {
    font-size: 1rem !important;
  }
}

.main-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-top: 1.25rem;
}

.typed-text {
  font-size: 2rem;
  color: white;
}

.header-button {
  text-transform: uppercase;
  text-decoration: none !important;
  border-radius: 1.25rem;
  padding: 0.625rem 0.9375rem;
  background-color: var(--primary-green);
  color: black;
  transition: 0.5s all ease-in-out;
  margin-top: 0.625rem;
  z-index: 2;
}

.header-button:hover {
  transform: scale(1.15);
  color: black;
}

.photo-wrapper{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px !important;
}
.about-img {
    width: 16rem;
    border-radius: 70%;
    padding: .3rem;
    border: 3px solid var(--primary-green);
}

strong {
    color: var(--primary-green);
}

.col-lg-6 > h1 {
color: var(--primary-green);
}

hr {
    color: var(--primary-green);
}

@media(max-width: 468px) {
    .about-img{
        width: 10rem;
    }
}
.skills {
    background-color: #f4f4f4;
    padding-bottom: 10px;
}

.container {
    text-align: center;
}

.skills-header {
    margin-bottom: 50px;
    color: var(--primary-green);
}

.box {
    margin-top: 20px;
    padding: 15px;
    background-color: white;
    border-bottom: 5px solid var(--primary-green);
    transition: 0.5s all ease-in-out;
    border-radius: 10px;
    height: 280px;
}

@media(max-width: 768px) {
    .box {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 10px;
    }
}

.box:hover {
    transform: translate(0,-20px);
    background-color: var(--primary-green);
}

.box-img {
    width: 100%; 
    height: 140px;
    margin-bottom: 20px;
}

.box h3 {
    font-size: 1.3rem;
}

.box p {
    font-size: 0.75rem;
    color: gray
}

.row-personal {
    display: flex; 
    justify-content: center;
    margin-top: 10px;
}
.experience {
    background-color: black;
    color: var(--primary-green);
padding: 0.7rem;
}

.experience-header {
    color: var(--primary-green);
    padding-top: 40px;
    padding-bottom: 20px;
}

.experience-wrapper {
    background-color: black;
    position: relative;
    padding: 3.125rem;
    overflow: hidden;
}

.experience-wrapper::before {
    position: absolute;
    content: "";
    top:0;
    left: 50%;
    margin-left: -0.0625rem;
    width: 0.125rem;
    height: 100%;
    background-color: white;
    z-index: 1;
}

.timeline-block {
    width: calc(50% + 0.5rem);
    display: flex;
    justify-content: space-between;
    clear: both;
}

.timeline-block-right {
    float:right;
}

.timeline-block-left {
    float: left;
    direction: rtl;
}

.marker {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    border: 0.3125rem solid white;
    background: var(--primary-green);
    margin-top:0.625rem;
    z-index: 3;
}

.timeline-content {
    width: 95%;
    padding: 0 0.9375rem;
    color: white;
    direction: ltr;

}

.timeline-content h3 {
        margin-top: 0.3125rem;
        color: rgb(0,191,166, 0.7);
}

@media(max-width: 768px) {
    .experience .container {
        padding-left: 0;
    }
    .experience-wrapper::before {
        left: 0.5rem;
        margin-bottom: 1.875rem;
    }
    .timeline-block-right {
        float:none;
        width: 100%;
    }
    .timeline-block-left {
        float: none;
        direction: ltr;
        width: 100%;
    }
}

.timeline-content > p {
    font-size: 0.75rem;
    color:gray;
    text-align: left !important;
}

.statement {
    color: white;
    margin-top: 50px;   
    padding: 40px;
}

.education {
    padding: 40px;
}

.education > h1 {
    text-align: center;
    margin-bottom: 40px;
}

.row-education {
    margin-top: 30px;
}

.education-photo-wrapper > img {
    width: 50%;
    display: block;
    margin-left: auto;
    margin-right: auto

}
.education-text-wrapper { 
    text-align: center;
    color: white;   
}


.portfolio-wrapper {
    background: white;
    padding: 3rem 0;
}

.container > h1 {
    color: var(--primary-green);
}

.portfolio-image {
    width: 20rem;
    height: 11rem;
}

.portfolio-image-box {
    position: relative;
    margin: 1rem;
    max-width: 20rem;
}

.overflow {
    position: absolute;
    opacity: 0;
    background: var(--primary-green);
    width: 20rem;
    height: 11rem;
    top: 0; 
    cursor: pointer;
}

.portfolio-image-box:hover .overflow{
    opacity: 0.6;
    transition: 0.4s ease-in-out;
}

.portfolio-icon {
    position: absolute;
    opacity: 0;
    bottom: 0.5rem;
    right: 0.5rem;
    color: white;
    font-size: 1.5rem;

}

.portfolio-image-box:hover .portfolio-icon {
    opacity: 1;
    transition: 0.4s ease-in-out;
    cursor: pointer;
}

.portfolio-image-popupbox {
    width: 45rem;
    max-height: 25rem;
    padding: 0 0.5rem;
    margin-bottom: 1rem;
}

.hyperlink {
    cursor: pointer;
    color:black;
}

.hyperlink:hover {
    color: var(--primary-green);
}

@media(max-width: 768px) {
    .portfolio-image-popupbox{
        width: 100%;
    }
}
.gallery {
    background-image: url(/resume/static/media/gallery-bg.75612be7.jpg);
    background-size: cover;
    text-align: center;
    padding-bottom: 4rem;
    padding-top: 4rem;
    margin-bottom: 0;
    width: 100%;
}

.gallery h1 {
    color: var(--primary-green);
    padding: 3rem 0;
}

.gallery-content {
    text-align: center;
    max-width: 53.125rem;
    margin: 0 auto 2rem auto;
    background: rgb(0,0,0,0.65);
}
.myCarousel {
    color: white;
    background: transparent;
    margin-top: -6%;
    width: 54%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 6%;
    padding-bottom: 8%;
    padding-left: 5%;
    padding-right: 5%;
    height: 17.875rem;
}

.myCarousel h3 {
    color: var(--primary-green);
    font-weight: 400;
    padding-top:3rem;
}

.myCarousel p {
    color: lightgray;
}

.carousel .slide img {
    width: 10rem !important;
    border-radius: 50%;
    padding-top: 2rem;
}

.carousel .slide {
    background: transparent !important;
    height: 100%;
}

.carousel-root {
    margin:auto !important;
    margin-top:3% !important;
}
.contacts {
    background-color: black;
    padding: 3rem 0;
    color: white;
}

.contacts h1 {
    color: var(--primary-green);
    padding: 1.3rem 0;
}

.contacts p {
    color: lightgray;
    font-size: 1.2rem;
}

.contacts input, .contacts textarea {
    width: 100%;
    box-sizing: border-box;
    background: none; 
    border: 0;
    border-bottom: 2px solid var(--primary-green);
    border-radius: 0;
    color: gray;
    outline: 0 !important;
    box-shadow: none !important;
    top: 0;
    margin-bottom: 1rem;
    padding-left: 0 !important;
}

.contacts .container {
    padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
    background: transparent;
    color: white;
    border-bottom: 2px solid var(--primary-green);
}

.contacts textarea {
    height: 10.5rem;
    margin: 2rem 0;
}

.contact-btn {
    margin: 1rem 0 0 0;
    width: 100%;
    background: var(--primary-green);
    border: none;
    border-radius: 1rem;
    transition: 0.5s all ease-in-out;
}

.contact-btn:hover {
    transform: scale(1.15);
    color: var(--primary-green);
    background: rgb(255,255,255, 0.2)
}

@media(max-width: 768px) {

    .contactcs {
        overflow-x: hidden;
    }
    .contacts .container {
        padding: 0 1rem;
    }

    .contacts p {
        padding: 0 0.4rem;
    }
}

.form-control{
    margin: 0;
}

.error-message {
    color: red;
    font-size: 0.8rem;
}

.succes-message {
    color: var(--primary-green)
}

.profile-links {
    display: flex;
    flex-direction: column;
    margin-top: 1.5rem;

}

.profile-links > a {
    text-decoration: none;
    margin-bottom: 1rem;
    color: white;
}

a:hover {
    color: var(--primary-green) !important;
}

.profile-icon {
    color: var(--primary-green) !important;
    height: 2rem;
    width: 2rem;
}
